<template>
  <div class="post-wrap">
    <h2 class="post-title">-&nbsp;&nbsp;-</h2>
    <ul>
        <li>
            <router-link to="/admin/post"><h3>~~~  ~~~</h3></router-link>
        </li>
        <li>
            <router-link to="/admin/posts"><h3>~~~  ~~~</h3></router-link>
        </li>
        <li>
            <router-link to="/admin/categories"><h3>~~~  ~~~</h3></router-link>
        </li>
        <li>
            <router-link to="/admin/tags"><h3>~~~  ~~~</h3></router-link>
        </li>
        <li>
            <router-link to="/admin/friendlinks"><h3>~~~  ~~~</h3></router-link>
        </li>
    </ul>
</div>
</template>

<script>
import axios from 'axios'
export default {
  created () {
    console.log('token')
    if (!this.$cookies.isKey('token')) {
      console.log('token')
      this.$router.push({ path: '/auth' })
    } else {
      console.log(this.$cookies.get('token'))
      console.log('token')
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$cookies.get('token')
    }
  }
}
</script>

